.covid_list_container{
  display: grid;
  grid-template-columns: repeat(auto-fill,minmax(400px,1fr));
  gap: 30px;
}

.list_filter{
  display: flex;
  align-items: center;
  gap: 20px;
  margin-bottom: 15px;
  .search_box{
    display: flex;
    align-items: center;
    gap: 15px;
    span{
      color: #444;
      font-size: 18px;
    }
  }
}

@media only screen and (max-width: 600px){
  .covid_list_container{
    display: grid;
    grid-template-columns: repeat(auto-fill,minmax(280px,1fr));
    gap: 30px;
  }
  .list_filter{
    flex-direction: column;
    align-items: flex-start;
  }
}